















































import { Component, Vue } from 'vue-property-decorator'

@Component({
  name: 'GanCanlender'
})
export default class GanCanlender extends Vue {
  year: number = 0
  month: number = 0
  day: number = 0
  week: string[] = ['', '', '', '', '', '', '']
  monthList: number[] = [31, 28, 31, 30, 31, 30, 31, 31, 30, 31, 30, 31]
  weekDay: number = 1
  lastWeekDay: number = 1
  // 
  get preMonthSize() {
    return this.month - 1 === 0 ? 31 : this.monthList[this.month - 2]
  }

  created() {
    this.getCurrentDate()
    this.initDate()
  }

  // 
  getCurrentDate() {
    const date = new Date()
    this.year = date.getFullYear()
    this.month = date.getMonth() + 1
    this.day = date.getDate()
  }

  // 
  getWeekDay(year: number, month: number, day: number) {
    return new Date(`${year}/${month}/${day}`).getDay()
  }

  initDate() {
    if (
      (this.year % 4 === 0 && this.year % 100 !== 0) ||
      this.year % 400 === 0
    ) {
      this.monthList[1] = 29
    } else {
      this.monthList[1] = 28
    }
    // 1
    const firstDay = this.getWeekDay(this.year, this.month, 1)
    // 1
    this.weekDay = firstDay === 7 ? 0 : firstDay
    // 
    const remineDay = this.getWeekDay(
      this.year,
      this.month,
      this.monthList[this.month - 1]
    )
    this.lastWeekDay = remineDay === 7 ? 6 : 6 - remineDay
  }

  changeDate(type: string) {
    switch (type) {
      case 'preYear':
        this.year -= 1
        break
      case 'preMonth':
        // 1 121
        if (this.month === 1) {
          this.month = 12
          this.year -= 1
        } else {
          this.month -= 1
        }
        break
      case 'nextYear':
        this.year += 1
        break
      case 'nextMonth':
        // 12 11
        if (this.month === 12) {
          this.month = 1
          this.year += 1
        } else {
          this.month += 1
        }
        break
      default:
        break
    }
    this.initDate()
  }
}
